import React from 'react';
import { useLanguageContext } from '../contexts/LanguageContext';

interface LanguageAwareWrapperProps {
  children: React.ReactNode;
}

/**
 * Wrapper que fora re-renderizao quando o idioma muda
 * Usa o contexto de idioma para garantir re-renderizao
 */
export const LanguageAwareWrapper: React.FC<LanguageAwareWrapperProps> = ({ children }) => {
  const { language } = useLanguageContext();
  
  // Usar o idioma do contexto como key para forar re-montagem completa
  // Quando language muda, todo o componente filho  desmontado e remontado
  return <div key={language}>{children}</div>;
};

export default LanguageAwareWrapper;

