import { useTranslation } from 'react-i18next';
import { useMemo } from 'react';

/**
 * Hook seguro para tradues que garante que sempre retorna dados vlidos
 */
export const useTranslationSafe = () => {
  const { t, i18n } = useTranslation();

  /**
   * Funo para obter array de traduo de forma segura
   */
  const tArray = useMemo(() => {
    return <T>(key: string, options?: any): T[] => {
      try {
        const result = t(key, { ...options, returnObjects: true });
        
        // Se  um array vlido, retorna
        if (Array.isArray(result)) {
          return result as T[];
        }
        
        // Se retornou a prpria chave (traduo no encontrada)
        if (typeof result === 'string' && result === key) {
          console.warn(`Traduo no encontrada para chave: "${key}"`);
          return [];
        }
        
        // Se  null ou undefined
        if (result === null || result === undefined) {
          console.warn(`Traduo retornou null/undefined para chave: "${key}"`);
          return [];
        }
        
        // Se  um objeto mas no um array
        if (typeof result === 'object' && !Array.isArray(result)) {
          console.warn(`Traduo retornou objeto no-array para chave: "${key}"`, result);
          return [];
        }
        
        // Para qualquer outro caso
        console.warn(`Tipo inesperado para traduo "${key}":`, typeof result, result);
        return [];
        
      } catch (error) {
        console.error(`Erro ao traduzir chave "${key}":`, error);
        return [];
      }
    };
  }, [t]);

  /**
   * Funo para obter string de traduo de forma segura
   */
  const tString = useMemo(() => {
    return (key: string, options?: any): string => {
      try {
        const result = t(key, options);
        
        // Se retornou a prpria chave (traduo no encontrada)
        if (result === key) {
          console.warn(` Traduo no encontrada para chave: "${key}"`);
          console.warn(`i18n inicializado:`, i18n.isInitialized);
          console.warn(`Idioma atual:`, i18n.language);
          console.warn(`Recursos disponveis:`, Object.keys(i18n.store.data));
          return `[${key}]`;
        }
        
        // Se  null ou undefined
        if (result === null || result === undefined) {
          console.warn(`Traduo retornou null/undefined para chave: "${key}"`);
          return `[${key}]`;
        }
        
        // Retorna como string
        return String(result);
        
      } catch (error) {
        console.error(`Erro ao traduzir chave "${key}":`, error);
        return `[${key}]`;
      }
    };
  }, [t, i18n]);

  return {
    t: tString, // Funo t padro para strings
    tArray,     // Funo especfica para arrays
    tString,    // Funo especfica para strings
    i18n,
    // Manter a funo original para casos especiais
    tOriginal: t
  };
};

export default useTranslationSafe;
